<template>
	<div>
		<div class="line"></div>
       <div class="form-box">
       	<span></span> <input type="text"  placeholder="620" id="txtMobile"  />
       </div>
       	<div class="line"></div>
       	<div class="form-box">
       	<span></span> <input type="password" placeholder="620" id="txtPassword"/>
       </div>
       <div class="line"></div>
       <div class="form-box">
       	<span></span> <input type="password"  placeholder="" id="tpassword"/>
       </div>
       <div class="line"></div>
       <div class="form-box">
       	<span></span> <input placeholder="" type="email" id="txtemail"/>
       </div>
        <div class="line"></div>
        <br />
         <mt-button type="danger" size="large" id="btnSubmit"></mt-button>
	</div>
</template>

<script>
	import Vue from 'vue'
	import { Field,Button } from 'mint-ui'
    Vue.component(Field.name, Field)
    Vue.component(Button.name, Button)

	export default{
		name:'registername',
		data () {
			return {
             
			}
		},
		mounted () {
			// 
	//
	var k1=false;
	var k2=false;
	var k3=false;
	var k4=false;
	
	$("#txtMobile").blur(function(){
		var reg=/^\w{6,20}$/;
		var str=$("#txtMobile").val();
		if(!reg.test(str)){
			alert("")
		
		}
		else{
			k1=true;
		}
	})
	
	//
	$("#tpassword").blur(function(){
		if(!($("#tpassword").val()==$("#txtPassword").val())){
			
			alert("")
		}
		else{
			k4=true;
		}
	})
  //
	$("#txtemail").blur(function(){
		var reg=/^([a-z0-9_\.-]+)@([\da-z\.-]+)\.([a-z\.]{2,6})$/;
		var str=$("#txtemail").val();
		if(!reg.test(str)){
			alert("")
		
		}
		else{
			k3=true;
		}
	})	
		
	//
	$("#txtPassword").blur(function(){
		var reg=/^\w{6,18}$/;
		var str=$("#txtPassword").val();
		if(!reg.test(str)){
			alert('')
		
		}
		else{
			
			k2=true;
		}
	})
		//
		$("#btnSubmit").click(function(){
    	if(k1&&k2&&k3&&k4){
    	 var deff = $.ajax({
			type:"get",
			url:"http://datainfo.duapp.com/shopdata/userinfo.php",
			data:{
				status:"register",
				userID:$("#txtMobile").val(),
				password:$("#txtPassword").val()
			}
		});
		deff.done(function(msg){  //
			switch(msg){
				case "0" : alert("");break;
				case "1" : alert("3");
							setTimeout(function(){
								location.href = "#/login";
							},3000)
						   break;
				case "2" : alert("");break;
			}
		})
		deff.error(function(){ //
			alert("");
		})
    	}
    	else{
    		alert("");
    	}
    })
	
		}
	}

</script>

<style scoped="scoped" lang="scss">
a{
	text-decoration: none;
}
 .line{
 	width:98%;
 	height:1px;
 	margin-left:2% ;
 	/*float: right;*/
 	border-top:1px solid #DDDDDD;
 }
 .form-box{
 	font-size:.45rem;
 	width:100%;
 	height:1.3rem;
 	display: flex;
 	span{
 		display: inline-block;
 		width:30%;
 		height:1.3rem;
 		line-height: 1.3rem;
 		text-indent: 10px;
 	}
 	input{
 	     border: none ;
 	     outline: none;
 	}
 }
</style>


